import React from 'react';

const Splash = {
  introduction: <React.Fragment>The following is the HTML WebFlavor Template for web based courses and learning. This demo course outlines many components that can be used in different HTML courses built from this template.
  <br/>
  <br/>
  Games, interactives, gating, assessments, and more are all included. Click <span className="bolded">Begin Course</span> to start.</React.Fragment>
};

export default Splash;



